.interior {
    padding-top: 150px;
    position: relative;
}

.interior__wrapper-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 450px 430px 405px;
    grid-auto-rows: 450px;
    margin-top: 60px;
    gap: 30px 40px;
}

.interior__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: var(--border-radius);
}


.interior__img:nth-child(1) {
    grid-column: 1 / 3;
}

.interior__img:nth-child(2) {
    grid-column: 1/2;
    grid-row: 2/2;
}

.interior__img:nth-child(3) {
    grid-column: 2 / 3;
    grid-row: 2 / 4;
}

.interior__img:nth-child(4) {
    grid-column: 1/2;
    grid-row: 3/4;
}

.swiper {
    display: none;
    z-index: 0;
}

.interior__button-container {
    text-align: center;
}

.interior__button {
    display: inline-block;
    padding: 12px 30px;
    margin-inline: auto;
    margin-top: 40px;
    vertical-align: middle;
    text-align: center;
    font-size: 22px;
    color: var(--color-light-blue);
}